body {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  overflow-x: hidden;
}

p,
ul,
li,
a,
span {
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
